<template><section>  
<h2 id="rate-ping-fen"><a class="header-anchor" href="#rate-ping-fen" aria-hidden="true">¶</a> Rate 评分</h2>
<p>常用评分组件</p>
<h3 id="ji-ben-yong-fa"><a class="header-anchor" href="#ji-ben-yong-fa" aria-hidden="true">¶</a> 基本用法</h3>
<p>可点击，进行选择评分。通过传入<code v-pre="">colors</code>可改变选择不同分值时的颜色效果，不传入，则默认不改颜色。</p>
<docs-demo-block> 
                                                <div class="source" slot="source"><fm-rate v-model="value" @change="getScore"></fm-rate>
<fm-rate v-model="value" @change="getScore" :colors="[&quot;#99A9BF&quot;, &quot;#F7BA2A&quot;, &quot;#FF9900&quot;]"></fm-rate>

  
</div>
                                                <pre v-pre=""><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">fm-rate</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"value"</span> <span class="hljs-attr">@change</span>=<span class="hljs-string">"getScore"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">fm-rate</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">fm-rate</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"value"</span> <span class="hljs-attr">@change</span>=<span class="hljs-string">"getScore"</span> <span class="hljs-attr">:colors</span>=<span class="hljs-string">'["#99A9BF", "#F7BA2A", "#FF9900"]'</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">fm-rate</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data() {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">value</span>: <span class="hljs-literal">null</span>
      }  
    },
    <span class="hljs-attr">methods</span>: {
      getScore(data) {
        alert(<span class="hljs-string">`您得到 <span class="hljs-subst">${data * <span class="hljs-number">2</span>}</span> 分啦！ლ(′◉❥◉｀ლ)`</span>);
      }
    }
  };
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>  
</code></pre>
</docs-demo-block>
<h3 id="xian-shi-fen-zhi"><a class="header-anchor" href="#xian-shi-fen-zhi" aria-hidden="true">¶</a> 显示分值</h3>
<p>可选择显示评分。通过传入<code v-pre="">score-text</code>可改变选择不同类型分值，不传入，则默认为<code v-pre="">2, 4, 6, 8, 10</code>。</p>
<docs-demo-block> 
                                                <div class="source" slot="source"><fm-rate v-model="value" @change="getScore" show-text=""></fm-rate>
<fm-rate v-model="value" @change="getScore" :score-text="[&quot;极差&quot;, &quot;一般&quot;, &quot;不错&quot;, &quot;很好&quot;, &quot;惊喜&quot;]" show-text=""></fm-rate>

  
</div>
                                                <pre v-pre=""><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">fm-rate</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"value"</span> <span class="hljs-attr">@change</span>=<span class="hljs-string">"getScore"</span> <span class="hljs-attr">show-text</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">fm-rate</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">fm-rate</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"value"</span> <span class="hljs-attr">@change</span>=<span class="hljs-string">"getScore"</span> <span class="hljs-attr">:score-text</span>=<span class="hljs-string">'["极差", "一般", "不错", "很好", "惊喜"]'</span> <span class="hljs-attr">show-text</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">fm-rate</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data() {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">value</span>: <span class="hljs-literal">null</span>
      }  
    },
    <span class="hljs-attr">methods</span>: {
      getScore(data) {
        alert(<span class="hljs-string">`您得到 <span class="hljs-subst">${data * <span class="hljs-number">2</span>}</span> 分啦！ლ(′◉❥◉｀ლ)`</span>);
      }
    }
  };
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>  
</code></pre>
</docs-demo-block>
<h3 id="zhi-du"><a class="header-anchor" href="#zhi-du" aria-hidden="true">¶</a> 只读</h3>
<p>显示评分，不可点击，可选择是否显示分值。</p>
<docs-demo-block> 
                                                <div class="source" slot="source"><fm-rate v-model="value1" disabled></fm-rate>
<fm-rate v-model="value2" show-text="" disabled></fm-rate>

  
</div>
                                                <p><code>disabled</code>设置不可点击，<code>show-text</code>设置显示分值。</p>
<pre v-pre=""><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">fm-rate</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"value1"</span> <span class="hljs-attr">disabled</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">fm-rate</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">fm-rate</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"value2"</span> <span class="hljs-attr">show-text</span> <span class="hljs-attr">disabled</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">fm-rate</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data() {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">value1</span>: <span class="hljs-number">3</span>,
        <span class="hljs-attr">value2</span>: <span class="hljs-number">4</span>
      }  
    }
  };
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>  
</code></pre>
</docs-demo-block>
<h3 id="shu-xing"><a class="header-anchor" href="#shu-xing" aria-hidden="true">¶</a> 属性</h3>
<table class="table"><thead>
<tr>
<th style="text-align:left">参数</th>
<th style="text-align:left">说明</th>
<th style="text-align:left">类型</th>
<th style="text-align:left">可选值</th>
<th style="text-align:left">默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td style="text-align:left">max</td>
<td style="text-align:left">最大分值</td>
<td style="text-align:left">number</td>
<td style="text-align:left">-</td>
<td style="text-align:left">5</td>
</tr>
<tr>
<td style="text-align:left">show-text</td>
<td style="text-align:left">是否显示分值</td>
<td style="text-align:left">boolean</td>
<td style="text-align:left">-</td>
<td style="text-align:left">false</td>
</tr>
<tr>
<td style="text-align:left">score-text</td>
<td style="text-align:left">分值显示类型数组</td>
<td style="text-align:left">array</td>
<td style="text-align:left">-</td>
<td style="text-align:left">['2 分', '4 分', '6 分', '8 分', '10 分']</td>
</tr>
<tr>
<td style="text-align:left">text-color</td>
<td style="text-align:left">分值文字颜色</td>
<td style="text-align:left">string</td>
<td style="text-align:left">-</td>
<td style="text-align:left">'#ffbe58'</td>
</tr>
<tr>
<td style="text-align:left">colors</td>
<td style="text-align:left">icon颜色数组，共有 3 个元素，为 3 个分段所对应的颜色</td>
<td style="text-align:left">array</td>
<td style="text-align:left">-</td>
<td style="text-align:left">['ffbe58', 'ffbe58', 'ffbe58']</td>
</tr>
<tr>
<td style="text-align:left">disabled</td>
<td style="text-align:left">是否为只读</td>
<td style="text-align:left">boolean</td>
<td style="text-align:left">-</td>
<td style="text-align:left">false</td>
</tr>
</tbody>
</table>
</section></template>

<script>
  export default {
    data() {
      return {
        value: null,
        value1: 3,
        value2: 4
      }  
    },
    methods: {
      getScore(data) {
        alert(`您得到 ${data * 2} 分啦！ლ(′◉❥◉｀ლ)`);
      }
    }
  };
</script>


